@charset "UTF-8";/* ###################  BASE STYLES ################## */* {
  padding: 0;
  margin: 0;
}/* #################### COLORS #####################  *//* #################### TIPOGRAPHY #################  *//* ###################  HEADER #################### *//* ###################  HEADER #################### *//* variables*/.header-nav-link {
  color: white;
  text-decoration: none;
  cursor: pointer;
  transition: all ease 1s;
}

.header-nav-link:hover {
  font-weight: bold;
  color: #bb3176;
}

.heading {
  font-family: Rubik, sans-serif;
  font-size: 22px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.05;
  letter-spacing: normal;
  text-align: center;
  color: #14d9c4;
}

.header {
  display: flex;
  flex-wrap: wrap;
  font-family: Rubik, sans-serif;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  z-index: 999;
}

.header-base {
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.95;
  background-color: #099d8d;
  flex: 1;
  height: 60px;
}

.GroupName {
  line-height: 0.7;
  transform: scale(0.75);
}

.header-nav {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
  background-color: #14d9c4;
}

.header-nav-list {
  display: flex;
  justify-content: space-evenly;
  opacity: 0.9;
  width: 100%;
  list-style-type: none;
}

.header-nav-item {
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal;
  color: white;
  text-decoration: none;
}/* ###################  HERO #################### */.main__hero {
  background: url("../images/img-hero1.jpg") top no-repeat;
  background-size: cover;
  height: 421px;
  position: relative;
}

.main__hero-text {
  position: absolute;
  color: black;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 600;
  bottom: 0;
  background-color: #fff;
  width: 80%;
  left: 50%;
  transform: translate(-50%, 50%);
  padding: 10px;
  line-height: 1;
}

.main__hero-text > p {
  padding: 0px 10px;
}

.image-main {
  background-size: cover;
  width: 360px;
  height: 200px;
}/* ###################  TEAM #################### */section.anchor {
  padding-top: 105px;
  padding-bottom: 45px;
}

.container {
  margin: 0 15px;
}

.team {
  position: relative;
  padding: 76px 15px 30px;
}

.heading {
  text-transform: uppercase;
}

.team:after {
  content: "";
  width: 30px;
  height: 30px;
  background-color: #fff;
  position: absolute;
  left: 44%;
  top: 95%;
  transform: rotate(45deg);
}

.team .description {
  margin-bottom: 15px;
}/* ###################  SKILLS ################## *//* ###################  SKILLS ################## */.skills {
  background-color: #b8b8b9;
  padding: 0px 15px 45px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skills .skill {
  text-align: center;
  padding: 30px 20px 0 30px;
}

.skills .skill .heading {
  text-transform: uppercase;
}

.skills .skill .heading .skill-content {
  color: #54585a;
}/* ##############  CONTAINER ALLS  ############## *//*Variable*/.heading {
  font-family: Rubik;
  font-size: 24px;
  font-weight: bold;
  line-height: 1.88;
  text-align: center;
  color: #099d8d;
}

.adalaber * {
  box-sizing: border-box;
}

.adalaber {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 25px 20px 0 20px;
}

.adalaber .adalaber__img {
  width: 180px;
  border: 5px solid lightgrey;
  border-radius: 5px;
  margin-bottom: 10px;
  transition: border-radius ease 1s;
}

.adalaber .adalaber__img:hover {
  border-radius: 50%;
}

.adalaber .adalaber__name {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  text-align: center;
  font-weight: 700;
  font-size: 20px;
  line-height: 2.25;
  color: #000000;
}

.adalaber .adalaber__bio {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  text-align: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #54585a;
}

.adalaber .social__icons-list {
  list-style: none;
  padding-top: 15px;
}

.adalaber .social__icons-item {
  color: #099d8d;
  display: inline;
  padding: 0 3px 0 3px;
}

.adalaber .social__icons-link {
  text-decoration: none;
  color: #099d8d;
  cursor: pointer;
  transition: color 0.6s ease;
}

.adalaber .social__icons-link::after {
  content: " · ";
}

.adalaber .social__icons-link:hover {
  color: #bb3176;
}

.adalaber .icon::after {
  content: " ";
}/*Responsive: Add grid to medium devices*//*Responsive: Add grid to large devices*//* ###################  FOOTER  #################### */.footer {
  width: 100vw;
  font-size: 14px;
  text-align: center;
  color: #b8b8b9;
  font-family: "Open Sans", sans-serif;
  border-top: 1px solid #b8b8b9;
  border-top-color: #b8b8b9;
}

.footer-links {
  display: block;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0px 45px 0px;
  text-transform: uppercase;
}

.footer-nav {
  padding-top: 20px;
  padding-bottom: 30px;
}

.footer-nav-ul {
  padding-left: 0;
  margin-top: 20px;
  padding-left: 0;
}

.footer-nav-li {
  list-style: none;
  width: 264;
  height: 19;
}

.footer-link {
  text-decoration: none;
  color: #54585a;
  font-weight: bold;
}

@media all and (min-width: 768px) {
  .header .header {
    flex-wrap: nowrap;
  }

  .header-base .header-base {
    background-color: #14d9c4;
    justify-content: flex-start;
    padding-left: 45px;
  }

  .header-nav {
    background-color: #099d8d;
    height: 60px;
    width: 50%;
    animation-duration: 3s;
    animation-name: animation-header;
    animation-iteration-count: 1;
  }

  .header-nav {
    background-color: #099d8d;
    height: 60px;
    width: 50%;
    list-style-type: none;
  }

  .main__hero-text {
    opacity: 0.8;
    left: 6%;
    transform: none;
    width: 54%;
  }

  .main__hero-text > p {
    padding: 10px 12px;
  }

  .team {
    padding: 45px;
    padding-bottom: 60px;
  }

  .team:after {
    left: 47%;
  }

  .skills {
    flex-direction: row;
    align-items: flex-start;
    padding: 45px 30px 60px;
  }

  .skills .skill {
    flex-basis: 50%;
    padding-top: 0;
    margin: 0 15px;
  }

  .adalaber {
    margin-top: 60px;
  }

  .adalaber .adalaber__img {
    width: 205px;
  }

  .adalaber {
    display: grid;
    grid-template-columns: 205px auto;
    grid-column-gap: 30px;
    grid-template-areas: "adalaber-image adalaber-information";
  }

  .adalaber .adalaber__grid-info {
    display: grid;
    grid-template-rows: auto auto auto;
    justify-items: flex-start;
    grid-area: adalaber-information;
  }

  .adalaber .adalaber__grid-info .adalaber__bio {
    text-align: left;
  }

  .adalaber .adalaber__grid-img {
    grid-area: adalaber-image;
  }

  .adalabers__list > li:nth-child(even) {
    grid-template-columns: auto 205px;
    grid-template-areas: "adalaber-information adalaber-image";
  }

  .adalabers__list > li:nth-child(even) .adalaber__grid-info {
    justify-items: flex-end;
  }

  .adalabers__list > li:nth-child(even) .adalaber__grid-info .adalaber__bio {
    text-align: right;
  }

  .footer {
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
  }

  .footer-links {
    padding-bottom: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  .footer-nav {
    display: flex;
    justify-content: flex-end;
    padding: 0;
    margin: 0px 48px 0px 48px;
    position: relative;
    flex-basis: 40%;
    margin-right: 45px;
  }

  .footer-nav-ul {
    margin-top: 0;
  }

  .footer-nav-li {
    text-align: right;
  }

  .image {
    width: 115.9px;
    height: 45px;
    transform: translateY(-70%);
    margin-left: 45px;
  }

  .team-name {
    flex-basis: 40%;
    text-align: left;
    padding-left: 45px;
  }
}

@media all and (min-width: 1200px) {
  .team {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .team p {
    width: 50%;
  }

  .team:after {
    left: 50%;
  }

  .skills {
    justify-content: center;
  }

  .skills .skill {
    flex-basis: 40%;
  }

  .adalaber .adalaber__img {
    width: 278px;
  }

  .adalaber {
    grid-template-columns: 278px 439px;
    grid-column-gap: 45px;
    justify-content: center;
  }

  .adalabers__list > li:nth-child(even) {
    grid-template-columns: 439px 278px;
  }
}

@media all and (min-width: 1490px) {
  .footer-nav {
    flex-basis: 45%;
  }
}

@keyframes animation-header {
  from {
    margin-left: 100%;
    width: 100%;
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}
